@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;800&display=swap";
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  font-family: Poppins, sans-serif;
}

.dark {
  color: #fefefe;
  background-color: #333742;
}

.navigation {
  width: 100%;
  min-height: 50px;
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
}

.dark .navigation {
  background-color: #3d4353;
}

.navigation .logo {
  color: #414f57;
  cursor: pointer;
  margin: 16px;
  padding: 10px 0;
  font-family: Poppins, sans-serif;
  font-weight: 800;
}

.dark .navigation .logo {
  color: #dfdfdf;
}

.navigation .logo span {
  font-family: inherit;
  font-weight: 500;
}

.navigation__bar {
  height: 60px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px #3448541a;
}

.navigation__menu_horizontal {
  width: 50%;
  max-width: 800px;
  height: 100%;
  margin-left: auto;
  margin-right: 72px;
  display: flex;
}

.navigation__menu_horizontal ul {
  width: 100%;
  height: 100%;
  justify-content: right;
  align-items: center;
  display: inline-flex;
}

.navigation__menu_horizontal ul li {
  min-width: 16.66%;
  height: 80%;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  transition: background-color .2s;
  display: flex;
}

.navigation__menu_horizontal ul li a {
  color: #768591;
  font-family: Poppins, sans-serif;
  font-size: .7rem;
  text-decoration: none;
  transition: color .2s;
}

.dark .navigation__menu_horizontal ul li a {
  color: #efefef;
}

.navigation__menu_horizontal ul li:hover {
  background-color: #768591;
}

.dark .navigation__menu_horizontal ul li:hover {
  background-color: #272a33;
}

.navigation__menu_horizontal ul li:hover > a {
  color: #fefefe;
}

.dark .navigation__menu_horizontal ul li:hover > a {
  color: #efefef;
}

.profile {
  min-width: 16.66%;
  height: 80%;
  justify-content: center;
  align-items: center;
  margin-left: 100px;
  display: flex;
}

.profile::hover {
  color: #fefefe;
}

.profile img {
  height: 30px;
  width: 30px;
  visibility: hidden;
  margin: 10px;
}

#profileName {
  visibility: hidden;
  color: #768591;
  font-size: .7rem;
  font-weight: 400;
}

.wrapper {
  min-width: 1200px;
  height: 99vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.main-box {
  height: 450px;
  width: 600px;
  backdrop-filter: blur(21px) saturate(43%);
  -webkit-backdrop-filter: blur(21px) saturate(43%);
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: none;
  box-shadow: 0 5px 15px #00000026;
}

.dark .main-box {
  background-color: #3d4353;
}

.msg-box {
  width: 100%;
  height: 83%;
  -ms-overflow-style: none;
  scrollbar-width: none;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
  overflow: scroll;
}

.msg-box::-webkit-scrollbar {
  display: none;
}

.notification {
  width: 100%;
