#textInputBox {
  height: 100%;
  width: 75%;
  color: #959595;
  background-color: #f5f5f5;
  border: none;
  outline: none;
  padding-left: 20px;
  font-size: 14px;
}

.dark #textInputBox {
  color: #efefef;
  background-color: #323d4a;
}

#imageInputButton {
  display: none;
}

#imagePreviewWrapper {
  width: 10%;
  height: 100%;
  display: none;
}

#imagePreview {
  height: 100%;
  width: 100%;
}

#imageClose {
