  display: flex;
}

.dark label {
  background-color: #323d4a;
  border-right: 2px solid #3d4353;
}

#sendNameForm {
  height: 50px;
  width: 400px;
  display: flex;
  box-shadow: 0 1px 4px #00000029;
}

#username {
  height: 100%;
  width: 80%;
  color: #6c6c6c;
  border: none;
  outline: none;
  padding-left: 20px;
}

.dark #username {
  color: #afafaf;
  background-color: #2b3142;
}

#sendName {
  height: 100%;
  width: 20%;
  color: #757575;
  background-color: #ffdb58;
  border: none;
}

.dark #sendName {
  color: #e0e0e0;
