  color: #6c6c6c;
  background-color: #ecebeb;
  padding: 10px;
  font-size: 12px;
}

.dark .msg-text {
  color: #afafaf;
  background-color: #2b3142;
}

.msg-image {
  width: 200px;
}

.msg-block {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.msg-block-close {
  margin-top: 3px;
}

.msg-block-own {
  width: 100%;
